import React from "react";
import { Paper, Typography, Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import Link from "components/CusLink";
import { useTranslation } from "react-i18next";
import { Link as RouterLink } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  paperBlock: (props) => ({
    padding: "30px 15px",
    backgroundImage: `url(/images/structuralSubdivision/${props.imagePath})`,
    backgroundPosition: "right bottom",
    backgroundRepeat: "no-repeat",
    backgroundSize: "auto 65%",
    minHeight: 200,
    [theme.breakpoints.up("lg")]: {
      minHeight: 250,
      padding: "40px 20px",
    },
    [theme.breakpoints.up("xl")]: {
      minHeight: 325,
      padding: "50px 30px",
    },
  }),
  typographyStyle: {
    width: "75%",
  },
}));

const StructuralBlock = ({ dataItem, picName, parentUrl }) => {
  const isWithLink = Boolean(dataItem.link);
  const isExternal = /^\/\/.*/.test(dataItem.link);
  return (
    <>
      {isWithLink ? (
        <Link
          component={RouterLink}
          rel="noopener"
          target={isExternal ? "_blank" : "_self"}
          to={isExternal ? dataItem.link : parentUrl + dataItem.link}
        >
          <BlockWithoutLink title={dataItem.name} picName={picName} />
        </Link>
      ) : (
        <BlockWithoutLink title={dataItem.name} picName={picName} />
      )}
    </>
  );
};

const BlockWithoutLink = ({ title, picName }) => {
  const { t } = useTranslation();

  const classes = useStyles({ imagePath: picName });
  return (
    <Paper square className={classes.paperBlock}>
      <Box>
        <Typography className={classes.typographyStyle}>{t(title)}</Typography>
      </Box>
    </Paper>
  );
};
export default StructuralBlock;
