import React from "react";
import { Box, IconButton } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Link from "components/CusLink";

import { ReactComponent as ArrowDownIcon } from "icons/chevron-down.svg";
import { Link as RouterLink } from "react-router-dom";

import IconWithMenu from "components/IconWithMenu";
import MenuIcon from "@material-ui/icons/Menu";
import Drawer from "components/navbar/components/Drawer";

const useStyles = makeStyles((theme) => ({
  root: {
    height: 25,
    width: "100%",
  },
  menuItem: {
    marginRight: 17,
  },
  linkStyle: {
    color: "black",
    cursor: "pointer",
  },
  leftUpper: {
    marginRight: "auto",
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
}));

const UpperNavbar = ({ closePaperMenu, hideMenu, menuItems }) => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  React.useEffect(() => {
    if (!hideMenu) handleDrawerClose();
  }, [hideMenu]);

  return (
    <>
      <Box
        className={classes.root}
        display="flex"
        alignItems="center"
        justifyContent="flex-end"
      >
        <Box justifySelf="flex-start" className={classes.leftUpper}>
          {hideMenu ? (
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerOpen}
              className={classes.menuButton}
            >
              <MenuIcon />
            </IconButton>
          ) : (
            <Link component={RouterLink} onClick={closePaperMenu} to="/">
              <img alt="UNG logo" src="/images/logo.png" width="150" />
            </Link>
          )}
        </Box>
        {/* <Box className={classes.menuItem}>
          <Link
            onClick={() => {
              changeCurrentBlock("disability");
            }}
            underline="none"
            className={classes.linkStyle}
          >
            <IconWithText
              Icon={<VisionIcon />}
              text={t("For people with disabilities")}
            />
          </Link>
        </Box> */}
        <Box className={classes.menuItem}>
          <IconWithMenu Icon={<ArrowDownIcon />} reverse />
        </Box>
        {/* <Box className={classes.menuItem}>
          <Link
            underline="none"
            className={classes.linkStyle}
            onClick={() => {
              changeCurrentBlock("search");
            }}
          >
            <SearchIcon />
          </Link>
        </Box> */}
      </Box>
      {hideMenu && (
        <Drawer
          open={open}
          handleDrawerOpen={handleDrawerOpen}
          handleDrawerClose={handleDrawerClose}
          menuItems={menuItems}
        />
      )}
    </>
  );
};

export default UpperNavbar;
