import React from "react";
import { Grid, Typography, Box } from "@material-ui/core";
import BlockLayout from "components/BlockLayout";
import { supervisorData } from "constants/supervisorData";
import { makeStyles } from "@material-ui/styles";
import HtmlConverter from "components/HtmlConverter";
import ManCard from "components/ManCard";
import { useTranslation } from "react-i18next";
import LastChangesText from "./LastChangesText";
import { fromDigsToFormat } from "utils/formatDate";

const useStyles = makeStyles({
  titleStyle: {
    marginTop: 8,
    marginBottom: 8,
  },
});

const SupervisorContainer = () => {
  const classes = useStyles();
  const { i18n } = useTranslation();
  const curLangData = supervisorData[i18n.language];

  return (
    <BlockLayout
      title="Supervisory Board"
      breadcrumbNames={["Home", "About us", "Supervisory Board"]}
    >
      <Typography className={classes.titleStyle}>
        {curLangData.paragraphTitle}:
      </Typography>
      <Grid container justify="center" spacing={3}>
        {curLangData.workers.map((item, i) => (
          <Grid key={i} item xs={12} sm={6} md={4} container justify={"center"}>
            <ManCard workerData={item} withoutDetailInfo />
          </Grid>
        ))}
      </Grid>
      <Box mt={4}>
        <Typography className={classes.titleStyle} align="center">
          {curLangData.listTitle}:
        </Typography>
        <HtmlConverter htmlString={curLangData.dataList} />
      </Box>
      <LastChangesText>
        {fromDigsToFormat({ day: 28, month: 12, year: 2020 }, i18n.language)}
      </LastChangesText>
    </BlockLayout>
  );
};

export default SupervisorContainer;
