import React from "react";
import { useParams } from "react-router-dom";
import PagesWithFilesAndSidebar from "components/filePages/PagesWithFilesAndSidebar";
import * as API from "constants/api";
import LoadingContainer from "components/LoadingContainer";

const PagesWithFilesAndSidebarContainer = ({
  title,
  breadCrumbsTitle,
  url,
}) => {
  const { subId, page } = useParams();

  return (
    <LoadingContainer
      api={API.SUBCATEGORIES_FILES + "/" + subId + "?page=" + page}
      component={PagesWithFilesAndSidebar}
      title={title}
      breadCrumbsTitle={breadCrumbsTitle}
      url={url}
    />
  );
};

export default PagesWithFilesAndSidebarContainer;
