import React from "react";
import {
  Table,
  TableBody,
  TableRow,
  TableCell,
  TableHead,
  Typography,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { useTranslation } from "react-i18next";
import clsx from "clsx";
import TableContainer from "@material-ui/core/TableContainer";

const useStyles = makeStyles({
  container: {
    width: "100%",
    overflowX: "auto",
  },
  headBold: {
    fontWeight: "normal",
    color: "#FFFFFF",
  },
  marginTable: {
    marginTop: 20,
  },
  headColor: {
    backgroundColor: "#03A3DF",
    opacity: "50%",
  },
  greenTitle: {
    color: "#27AE60",
  },
  redTitle: {
    color: "#EB5757",
  },
});

const TenderTable = ({ tenderInfoFinalTable }) => {
  const classes = useStyles();
  const { t } = useTranslation();

  return (
    <TableContainer className={classes.container}>
      <Table className={clsx(classes.marginTable)}>
        <TableHead className={classes.headColor}>
          <TableRow>
            {Object.keys(tenderInfoFinalTable).map((key, i) => (
              <TableCell
                key={i}
                classes={{ root: classes.headBold }}
                align="center"
              >
                <Typography component={"p"}>
                  {t(`tenderDetailInfoTable.${key}`)}
                </Typography>
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            {Object.keys(tenderInfoFinalTable).map((key, i) => (
              <TableCell
                key={i}
                align="center"
                className={clsx(
                  key === "Better price" &&
                    (tenderInfoFinalTable["Better price"]
                      ? classes.greenTitle
                      : classes.redTitle)
                )}
              >
                <Typography component={"p"}>
                  {key !== "Better price"
                    ? tenderInfoFinalTable[key]
                    : tenderInfoFinalTable["Better price"]
                    ? t("tenderDetailInfoTable.notSold")
                    : t("tenderDetailInfoTable.sold")}
                </Typography>
              </TableCell>
            ))}
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TenderTable;
