import React from "react";
import { Grid, Typography, Box, useMediaQuery } from "@material-ui/core";
import { makeStyles, useTheme } from "@material-ui/styles";

import BlockLayout from "components/BlockLayout";
import FuelBlock from "components/FuelBlock";
import { oilBaseData, products } from "constants/neftBazaData";
import ManCard from "components/ManCard";
import { Redirect, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";

const useStyles = makeStyles((theme) => ({
  itemPos: {
    margin: "0 10px 10px 0",
  },
  imageContainer: {
    height: 300,
    overflow: "hidden",
  },
  titleStyle: {
    marginBottom: 30,
    [theme.breakpoints.down("md")]: {
      marginBottom: 25,
    },
    [theme.breakpoints.down("sm")]: {
      marginBottom: 20,
    },
    [theme.breakpoints.down("xs")]: {
      marginBottom: 10,
    },
  },
  fuelBlockMargin: {
    marginBottom: 50,
    [theme.breakpoints.down("md")]: {
      marginBottom: 40,
    },
    [theme.breakpoints.down("sm")]: {
      marginBottom: 30,
    },
    [theme.breakpoints.down("xs")]: {
      marginBottom: 20,
    },
  },
}));

const tableRows = {
  name: "Initials",
  position: "Position",
  phoneNumber: "Phone number",
  freeHours: "Days and time of reception",
  address: "Legal address",
  email: "E-mail address",
  trustNumber: "Helpline",
};

const oilBaseNames = {
  1: "Takhiatash Oil Base LLC",
  2: "Andijan Oil Base LLC",
  3: "Bukhara Oil Base LLC",
  4: "Jizzakh Oil Base LLC",
  5: "Karshi Oil Base LLC",
  6: "Navoi Oil Base LLC",
  7: "Turakurgan Oil Base LLC",
  8: "Maroqand Oil Base LLC",
  9: "Termez Oil Base LLC",
  10: "Guliston Oil Base LLC",
  11: "Pakhta Oil Base LLC",
  12: "Chinobod Oil Base LLC",
  13: "Kuchlik Oil Base LLC",
  14: "Fergana Oil Base LLC",
  15: "Urgench Oil Base LLC",
};

const NeftBazaContainer = () => {
  const classes = useStyles();
  const { i18n, t } = useTranslation();

  const theme = useTheme();
  const isSmaller = useMediaQuery(theme.breakpoints.down("xs"));
  const { id } = useParams();

  const intId = parseInt(id);
  if (intId < 1 || intId > 15) {
    return <Redirect to={"/page404"} />;
  }
  const neftBazaItemInfo = oilBaseData[i18n.language][id];

  return (
    <BlockLayout
      title={oilBaseNames[id]}
      breadcrumbNames={[
        "Home",
        "About us",
        "Structural subdivision",
        oilBaseNames[id],
      ]}
    >
      <Typography className={classes.titleStyle} variant="h4">
        {t("Products manufactured by the joint-stock company")}
      </Typography>
      <Grid className={classes.fuelBlockMargin} container spacing={6}>
        <Grid container item xs={12}>
          <Box
            display={"flex"}
            flexWrap={"wrap"}
            flexDirection={"row"}
            justifyContent={isSmaller ? "center" : "flex-start"}
          >
            {products[i18n.language][id].map((productInfo, i) => (
              <FuelBlock key={i} productInfo={productInfo} />
            ))}
          </Box>
        </Grid>
        {/*<Grid item xs={12} md={6}>
          <Slider {...settings}>
            {neftBazaItemInfo["locationImages"].map((imageUrl) => (
              <Box className={classes.imageContainer}>
                <img
                  alt="Slider"
                  style={{ width: "100%" }}
                  src={`/images/neftBaza/${imageUrl}`}
                />
              </Box>
            ))}
          </Slider>
        </Grid>*/}
      </Grid>
      <Typography className={classes.titleStyle} align="center" variant="h4">
        {t("workersOfOilBase", { nameOfOilBase: t(oilBaseNames[id]) })}
      </Typography>
      <Grid container spacing={3}>
        {neftBazaItemInfo.map((itemInfo, i) => (
          <Grid
            key={i}
            item
            container
            justify={"center"}
            xs={12}
            sm={6}
            md={4}
            lg={3}
          >
            <ManCard tableRows={tableRows} workerData={itemInfo} />
          </Grid>
        ))}
      </Grid>
    </BlockLayout>
  );
};

export default NeftBazaContainer;
