import React from "react";
import { Typography } from "@material-ui/core";
import BlockLayout from "components/BlockLayout";
import LastChangesText from "components/LastChangesText";
import HtmlConverter from "components/HtmlConverter";
import { useTranslation } from "react-i18next";
import { fromDigsToFormat } from "utils/formatDate";

const TextLocalizationContainer = ({
  content,
  title,
  breadcrumbNames,
  lastChangesDate,
}) => {
  const { t, i18n } = useTranslation();

  return (
    <BlockLayout title={title} breadcrumbNames={[...breadcrumbNames, title]}>
      {t(`${content}.paragraphTitle`) !== "" && (
        <Typography align="center" variant="h4">
          {t(`${content}.paragraphTitle`)}
        </Typography>
      )}
      <HtmlConverter htmlString={t(`${content}.paragraphs`)} />

      <LastChangesText>
        {fromDigsToFormat(lastChangesDate, i18n.language)}
      </LastChangesText>
    </BlockLayout>
  );
};

export default TextLocalizationContainer;
