import React from "react";
import { Grid, Box } from "@material-ui/core";

import FileBlock from "components/FileBlock";
import BlockLayout from "components/BlockLayout";
import { useParams } from "react-router";
import PaginationContainer from "components/PaginationContainer";

const categoriesSerializer = (data) => {
  return data.results;
};

const PagesWithFiles = ({ data, breadCrumbsTitle, title, url }) => {
  const results = categoriesSerializer(data);
  const { id, subId } = useParams();

  return (
    <BlockLayout
      title={title}
      breadcrumbNames={["Home", ...breadCrumbsTitle, title]}
    >
      <PaginationContainer data={data} url={`${url}/${id}/sub/${subId}/`}>
        <Grid container spacing={3}>
          <Grid
            item
            container
            xs={12}
            md={8}
            lg={9}
            direction="column"
            justify="flex-start"
          >
            {results.map((fileInfo, i) => (
              <Box key={i} mb={3}>
                <Grid item>
                  <FileBlock fileInfo={fileInfo} />
                </Grid>
              </Box>
            ))}
          </Grid>
        </Grid>
      </PaginationContainer>
    </BlockLayout>
  );
};

export default PagesWithFiles;
