import React from "react";
import {
  Typography,
  Box,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Grid,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import BlockLayout from "components/BlockLayout";
import { useParams, Redirect } from "react-router-dom";
import { managementData } from "constants/managementData";
import { findIndexById } from "helpers/helperFunction";
import LastChangesText from "components/LastChangesText";
import { useTranslation } from "react-i18next";
import { fromDigsToFormat } from "utils/formatDate";

const tableRows = {
  name: "Initials",
  position: "Position",
  phoneNumber: "Phone number",
  freeHours: "Days and time of reception",
  email: "E-mail address",
  fax: "Fax",
};

const useStyles = makeStyles((theme) => ({
  image: {
    width: 250,
    marginRight: 20,
    [theme.breakpoints.down("xs")]: {
      width: 210,
    },
  },
  cellPadding: {
    padding: 0,
  },
  paragraphMargin: {
    marginBottom: 25,
  },
}));

const ManagerDetail = () => {
  const classes = useStyles();
  const { i18n, t } = useTranslation();
  const { id } = useParams();
  const intId = parseInt(id);
  if (intId < 1 || intId > 6) {
    return <Redirect to={"/page404"} />;
  }

  const indexOfArr = findIndexById(managementData[i18n.language], id);
  const managerData = managementData[i18n.language][indexOfArr];
  const bioArray = managerData.biography.split("\n");

  return (
    <BlockLayout
      title={managerData.name}
      breadcrumbNames={["Home", "About us", "Management", managerData.name]}
    >
      <Grid container spacing={2}>
        <Grid
          item
          xs={12}
          lg={3}
          md={4}
          container
          justify={"center"}
          alignItems={"flex-start"}
        >
          <img
            alt={managerData.name}
            src={`/images/management/${managerData.image}`}
            className={classes.image}
          />
        </Grid>
        <Grid item xs={12} lg={9} md={8}>
          <Table size="small">
            <TableBody>
              {Object.entries(tableRows).map((item, i) => (
                <TableRow key={i} style={{ padding: 0 }}>
                  <TableCell
                    className={classes.cellPadding}
                    component="th"
                    scope="row"
                  >
                    <Typography>{t(item[1])}</Typography>
                  </TableCell>
                  <TableCell className={classes.cellPadding}>
                    <Typography>{managerData[item[0]]}</Typography>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Grid>
      </Grid>
      <Box mt={3}>
        <Typography variant="h4">{t("Biography")}</Typography>
        {bioArray.map(
          (paragraph, i) =>
            Boolean(paragraph) && (
              <Typography className={classes.paragraphMargin} key={i}>
                {paragraph}
              </Typography>
            )
        )}
        <LastChangesText>
          {fromDigsToFormat({ day: 26, month: 12, year: 2020 }, i18n.language)}
        </LastChangesText>
      </Box>
    </BlockLayout>
  );
};

export default ManagerDetail;
