import React from "react";
import { Box } from "@material-ui/core";
import BlockLayout from "components/BlockLayout";
import HtmlConverter from "components/HtmlConverter";
import FileBlock from "components/FileBlock";
import { useTranslation } from "react-i18next";

const categoriesSerializer = (data) => {
  return data.results;
};

const TextWithFiles = ({ title, breadCrumbsTitle, data }) => {
  const results = categoriesSerializer(data);
  const { i18n } = useTranslation();

  return (
    <BlockLayout
      title={title}
      breadcrumbNames={["Home", ...breadCrumbsTitle, title]}
    >
      {results.map((fileInfo, i) => (
        <React.Fragment key={i}>
          <HtmlConverter
            htmlString={fileInfo[`description_${i18n.language}`]}
          />
          <Box mt={3} display={"flex"} justifyContent={"center"}>
            <Box width={"75%"}>
              <FileBlock
                fileInfo={{
                  ...fileInfo,
                  description_uz: "",
                  description_en: "",
                  description_ru: "",
                }}
              />
            </Box>
          </Box>
        </React.Fragment>
      ))}
    </BlockLayout>
  );
};

export default TextWithFiles;
