import React from "react";
import { Grid, Typography, Box, Link } from "@material-ui/core";
import BlockLayout from "components/BlockLayout";
import { makeStyles } from "@material-ui/core/styles";
import UngMap from "components/UngMap";
import {
  chairmanMail,
  ungMail,
  telephoneNumber,
  officeNumber,
  trustPhoneNumber,
  workingHours,
  buses,
  metroStation,
} from "constants/constants";
import { useTranslation } from "react-i18next";

const useStyles = makeStyles((theme) => ({
  dayBlockStyle: (props) => ({
    backgroundColor: props.isWeekDay ? "#03A3DF" : "#CCCCCC",
    color: "#FFFFFF",
    width: "100%",
    paddingTop: 12,
    paddingBottom: 12,
    borderRadius: 8,
  }),
  marginTypography: {
    marginBottom: 30,
    marginTop: 20,
  },
  dayBlock: {
    width: 100,
    [theme.breakpoints.up("xl")]: {
      width: 120,
    },
  },
  heightMap: {
    height: 500,
    width: "100%",
    [theme.breakpoints.up("lg")]: {
      height: 600,
      width: "90%",
    },
    [theme.breakpoints.up("xl")]: {
      height: 700,
      width: "85%",
    },
  },
}));

const daysOfWeek = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];

const ContactsContainer = () => {
  const classes = useStyles();
  const { t } = useTranslation();

  return (
    <BlockLayout
      title="Contacts"
      breadcrumbNames={["Home", "About us", "Contacts"]}
    >
      <Grid container>
        <Grid item md={6} xs={12}>
          <Typography>
            <Typography component="span" color="secondary">
              {t("Tel")}:
            </Typography>{" "}
            {telephoneNumber} <br />
            <Typography component="span" color="secondary">
              {t("Chancellery")}:
            </Typography>{" "}
            {officeNumber} <br />
            <Typography component="span" color="secondary">
              {t("Helpline")}:
            </Typography>{" "}
            {trustPhoneNumber} <br />
            <Typography component="span" color="secondary">
              {t("Our email address")}:
            </Typography>{" "}
            <Typography component="span" color="primary">
              <Link component={"a"} href={`mailto:${chairmanMail}`}>
                {chairmanMail}
              </Link>
              ,
              <Link component={"a"} href={`mailto:${ungMail}`}>
                {ungMail}
              </Link>
            </Typography>
          </Typography>
        </Grid>
        <Grid item md={6} xs={12}>
          <Typography>
            <Typography component="span" color="secondary">
              {t("Address")}:
            </Typography>{" "}
            {t("ungAddress")} <br />
            <Typography component="span" color="secondary">
              {t("Bus")}:
            </Typography>{" "}
            {buses} <br />
            <Typography component="span" color="secondary">
              {t("Metro")}:
            </Typography>{" "}
            {t(metroStation)} <br />
            <Typography component="span" color="secondary">
              {t("Landmark")}:
            </Typography>{" "}
            {t("Westminster International University in Tashkent")}
          </Typography>
        </Grid>
        <Box
          display="flex"
          flexDirection="column"
          alignItems="center"
          style={{
            width: "100%",
          }}
        >
          <Typography
            className={classes.marginTypography}
            align="center"
            variant="h4"
          >
            {t("Work schedule")}
          </Typography>
          <Box
            display={"flex"}
            justifyContent={"center"}
            flexWrap={"wrap"}
            mb={2}
          >
            {daysOfWeek.map((day) => (
              <DayBlock key={day} dayInfo={day} />
            ))}
          </Box>
        </Box>
        <Box display={"flex"} justifyContent={"center"} width={"100%"}>
          <Box className={classes.heightMap}>
            <UngMap />
          </Box>
        </Box>
      </Grid>
    </BlockLayout>
  );
};

const DayBlock = ({ dayInfo }) => {
  const isWeekDay = dayInfo !== "Sat" && dayInfo !== "Sun";
  const classes = useStyles({ isWeekDay: isWeekDay });
  const { t } = useTranslation();

  return (
    <Box display="inline-block" className={classes.dayBlock} mx={1}>
      <Box
        display="flex"
        justifyContent="center"
        flexDirection="row"
        alignItems="center"
        className={classes.dayBlockStyle}
      >
        <Typography variant="h5" style={{ fontWeight: "normal" }}>
          {t(dayInfo)}
        </Typography>
      </Box>
      <Typography align="center" variant="h5">
        {isWeekDay ? workingHours : "-"}
      </Typography>
    </Box>
  );
};

export default ContactsContainer;
