import React, { useRef, useLayoutEffect } from "react";
import Block from "components/Block";
import Title from "components/Title";
import Box from "@material-ui/core/Box";
import { useTranslation } from "react-i18next";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4geodata_uzbekistanLow from "@amcharts/amcharts4-geodata/uzbekistanLow";

am4core.useTheme(am4themes_animated);

const Map = () => {
  const { t } = useTranslation();
  const chart = useRef(null);

  useLayoutEffect(() => {
    // Create map instance
    let am4chart = am4core.create("chartdiv", am4maps.MapChart);
    // Set map definition
    am4chart.geodata = am4geodata_uzbekistanLow;
    // Set projection
    am4chart.projection = new am4maps.projections.Miller();
    // Create map polygon series
    const polygonSeries = am4chart.series.push(new am4maps.MapPolygonSeries());

    // Make map load polygon (like country names) data from GeoJSON
    polygonSeries.useGeodata = true;
    // Configure series
    const polygonTemplate = polygonSeries.mapPolygons.template;
    polygonTemplate.tooltipText = "{name}";
    polygonTemplate.polygon.fillOpacity = 0.6;
    // Create hover state and set alternative fill color
    const hs = polygonTemplate.states.create("hover");
    hs.properties.fill = am4chart.colors.getIndex(0);
    // Add image series
    const imageSeries = am4chart.series.push(new am4maps.MapImageSeries());
    imageSeries.mapImages.template.propertyFields.longitude = "longitude";
    imageSeries.mapImages.template.propertyFields.latitude = "latitude";
    imageSeries.mapImages.template.tooltipText = "{title}";
    imageSeries.mapImages.template.propertyFields.url = "url";

    const circle = imageSeries.mapImages.template.createChild(am4core.Circle);
    circle.radius = 3;
    circle.propertyFields.fill = "color";

    const circle2 = imageSeries.mapImages.template.createChild(am4core.Circle);
    circle2.radius = 3;
    circle2.propertyFields.fill = "color";

    circle2.events.on("inited", function (event) {
      animateBullet(event.target);
    });

    function animateBullet(circle) {
      const animation = circle.animate(
        [
          {
            property: "scale",
            from: 1,
            to: 5,
          },
          {
            property: "opacity",
            from: 1,
            to: 0,
          },
        ],
        1000,
        am4core.ease.circleOut
      );
      animation.events.on("animationended", function (event) {
        animateBullet(event.target.object);
      });
    }

    const colorSet = new am4core.ColorSet();

    imageSeries.data = [
      {
        title: t("Mubarak Gas Processing Plant"),
        latitude: 39.399042,
        longitude: 65.051079,
        color: colorSet.next(),
      },
      {
        title: t("Mubarak Oil and Gas Production Department"),
        latitude: 39.339949,
        longitude: 65.601992,
        color: colorSet.next(),
      },
      {
        title: t("Shurtan Oil and Gas Production Department"),
        latitude: 38.570654,
        longitude: 66.250222,
        color: colorSet.next(),
      },
      {
        title: t("Shurtan Gas Chemical Complex LLC"),
        latitude: 38.630496,
        longitude: 65.786051,
        color: colorSet.next(),
      },
      {
        title: t("Gasli Oil and Gas Production Department"),
        latitude: 40.12709,
        longitude: 63.45304,
        color: colorSet.next(),
      },
      {
        title: t("Ustyurt Gas Production Department"),
        latitude: 43.955944,
        longitude: 58.700929,
        color: colorSet.next(),
      },
      {
        title: t("“Uz-Kor Gas Chemical” LLC"),
        latitude: 43.004176,
        longitude: 58.825447,
        color: colorSet.next(),
      },
      {
        title: t("PJSC «LUKOIL»"),
        latitude: 38.230461,
        longitude: 66.501924,
        color: colorSet.next(),
      },
      {
        title: "АО «Андижаннефть»",
        latitude: 40.791656,
        longitude: 72.347952,
        color: colorSet.next(),
      },
    ];

    // disable zooming on scrolling
    am4chart.chartContainer.wheelable = false;

    chart.current = am4chart;

    return () => {
      am4chart.dispose();
    };
  }, [t]);

  return (
    <Block withBackground>
      <Title>{t("Our factories")}</Title>
      <Box display={"flex"} justifyContent={"center"}>
        <div id="chartdiv" style={{ width: "85%", height: 500 }} />
      </Box>
    </Block>
  );
};

export default Map;
