import { Typography, Grid } from "@material-ui/core";
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";

const useStyles = makeStyles((theme) => ({
  textStyle: (reverse) => ({
    marginLeft: reverse ? "0px" : "5px",
    marginRight: reverse ? "5px" : "0px",
    fontSize: 14,
    [theme.breakpoints.up("xl")]: {
      fontSize: 20,
    },
    [theme.breakpoints.down("sm")]: {
      fontSize: 12,
    },
    [theme.breakpoints.down("xs")]: {
      fontSize: 10,
      marginLeft: reverse ? "0px" : "2px",
      marginRight: reverse ? "2px" : "0px",
    },
  }),
}));

const IconWithText = ({
  Icon,
  text,
  style,
  reverse,
  textStyle,
  textClassName,
}) => {
  const classes = useStyles(reverse);

  return (
    <Grid
      container
      item
      direction={reverse ? "row-reverse" : "row"}
      alignItems="center"
      style={{
        ...style,
      }}
    >
      <Grid className={classes.iconStyle} item>
        {Icon}
      </Grid>
      <Grid item>
        <Typography
          style={{ ...textStyle }}
          className={clsx(classes.textStyle, textClassName)}
        >
          {text}
        </Typography>
      </Grid>
    </Grid>
  );
};

IconWithText.defaultProps = {
  text: "",
  style: {},
  reverse: false,
  textStyle: {},
};

export default IconWithText;
