import React from "react";
import BlockLayout from "components/BlockLayout";
import { managementData } from "constants/managementData";
import ManagerBlock from "components/ManagerBlock";
import { useTranslation } from "react-i18next";

const ManagementContainer = () => {
  const { i18n } = useTranslation();
  return (
    <BlockLayout
      title="Management"
      breadcrumbNames={["Home", "About us", "Management"]}
    >
      {managementData[i18n.language].map((item, i) => (
        <ManagerBlock key={i} managerData={item} />
      ))}
    </BlockLayout>
  );
};

export default ManagementContainer;
