import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { List, ListItem, ListItemText, Collapse } from "@material-ui/core";
import { ExpandMore, ExpandLess } from "@material-ui/icons";
import { find } from "ramda";
import { Link as RouterLink } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  nested: {
    paddingLeft: theme.spacing(5),
    paddingTop: 0,
    paddingBottom: 0,
  },
  itemPadding: {
    margin: 0,
  },
}));

const DrawerListItem = ({ menuItems, handleDrawerClose }) => {
  const classes = useStyles();
  const [selectedMenuId, setSelectedMenuId] = React.useState(0);
  const handleClick = (id) => {
    if (id === selectedMenuId) setSelectedMenuId(0);
    else setSelectedMenuId(id);
  };

  const childrenMenuItems = find(
    (item) => item.id === selectedMenuId,
    menuItems
  );

  return menuItems.map((menuItem) => (
    <React.Fragment key={menuItem.id}>
      {menuItem.hasOwnProperty("children") && menuItem.children.length > 0 ? (
        <ListItem button onClick={() => handleClick(menuItem.id)}>
          <ListItemText
            className={classes.itemPadding}
            primary={menuItem.name}
          />
          {selectedMenuId === menuItem.id ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
      ) : (
        <ListItem
          button
          component={RouterLink}
          to={menuItem.url}
          onClick={handleDrawerClose}
        >
          <ListItemText
            className={classes.itemPadding}
            primary={menuItem.name}
          />
        </ListItem>
      )}
      {childrenMenuItems !== undefined && selectedMenuId === menuItem.id && (
        <Collapse
          in={selectedMenuId === menuItem.id}
          timeout="auto"
          unmountOnExit
        >
          <List component="div" disablePadding>
            {childrenMenuItems.children.map((childMenu) => {
              let url =
                childMenu.hasOwnProperty("children") &&
                childMenu.children.length > 0
                  ? childMenu.url + childMenu.children[0]["url"]
                  : childMenu.url;
              url = menuItem.url !== null ? menuItem.url + url : url;
              const isExternal = /^\/\/.*/.test(url);
              return (
                <ListItem
                  key={childMenu.id}
                  button
                  component={RouterLink}
                  to={url}
                  target={isExternal ? "_blank" : "_self"}
                  className={classes.nested}
                  onClick={handleDrawerClose}
                >
                  <ListItemText
                    className={classes.itemPadding}
                    primary={childMenu.name}
                  />
                </ListItem>
              );
            })}
          </List>
        </Collapse>
      )}
    </React.Fragment>
  ));
};

export default DrawerListItem;
