import React from "react";
import BlockLayout from "components/BlockLayout";
import TextWithPics from "components/TextWithPics";

import { Typography } from "@material-ui/core";
import Paragraph from "components/Paragraph";
import LastChangesText from "components/LastChangesText";
import { fromDigsToFormat } from "../utils/formatDate";
import { useTranslation } from "react-i18next";

import { historyData } from "constants/historyData";

const MainContainer = () => {
  const { i18n } = useTranslation();

  return (
    <BlockLayout
      title="History of society"
      breadcrumbNames={["Home", "About us", "History of society"]}
    >
      {historyData[i18n.language].map((textBlock) => (
        <Paragraph title={textBlock.title}>
          {textBlock.paragraphs.map((paragraph, i) =>
            textBlock.images.length === 0 ? (
              <Typography>{paragraph}</Typography>
            ) : i === 0 ? (
              <TextWithPics images={textBlock.images}>
                <Typography>{paragraph}</Typography>
              </TextWithPics>
            ) : (
              <Typography>{paragraph}</Typography>
            )
          )}
        </Paragraph>
      ))}
      <LastChangesText>
        {fromDigsToFormat({ day: 20, month: 12, year: 2020 }, i18n.language)}
      </LastChangesText>
    </BlockLayout>
  );
};

export default MainContainer;
