import React from "react";
import { useParams } from "react-router-dom";
import TextWithFiles from "components/filePages/TextWithFiles";
import * as API from "constants/api";
import LoadingContainer from "components/LoadingContainer";

const TextWithFilesContainer = ({ title, breadCrumbsTitle }) => {
  const { subId } = useParams();

  return (
    <LoadingContainer
      api={API.SUBCATEGORIES_FILES + "/" + subId}
      component={TextWithFiles}
      title={title}
      breadCrumbsTitle={breadCrumbsTitle}
    />
  );
};

export default TextWithFilesContainer;
