import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Box, IconButton } from "@material-ui/core";
import { ReactComponent as CloseIcon } from "icons/cross.svg";

const useStyles = makeStyles((theme) => ({
  blockRelative: {
    position: "relative",
    width: "100%",
    height: "100%",
  },
  crossPosition: {
    position: "absolute",
    top: 10,
    right: 10,
    zIndex: 1000,
  },
  closeButton: {
    padding: 0,
  },
  imageStyle: {
    position: "absolute",
    bottom: 0,
    right: 0,
    height: 400,
    [theme.breakpoints.up("lg")]: {
      height: 465,
    },
    [theme.breakpoints.up("xl")]: {
      height: 520,
    },
  },
}));
const SideMenuImage = ({ closePaperMenu, imageSrc }) => {
  const classes = useStyles();
  return (
    <Box className={classes.blockRelative}>
      <Box className={classes.crossPosition}>
        <IconButton
          classes={{ root: classes.closeButton }}
          onClick={closePaperMenu}
        >
          <CloseIcon />
        </IconButton>
      </Box>
      <img className={classes.imageStyle} alt="Menu" src={imageSrc} />
    </Box>
  );
};

export default SideMenuImage;
