import React from "react";
import {
  IconButton,
  Typography,
  Box,
  Dialog,
  Link,
  Slide,
} from "@material-ui/core";
import MuiDialogTitle from "@material-ui/core/DialogTitle";

import BlockLayout from "components/BlockLayout";
import { withStyles } from "@material-ui/core/styles";
import LastChangesText from "components/LastChangesText";
import { ReactComponent as CloseIcon } from "icons/cross.svg";
import { useTranslation } from "react-i18next";
import { fromDigsToFormat } from "utils/formatDate";

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    padding: 0,
    width: 40,
    height: 40,
  },
});

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const ImageContainer = ({
  imgUrl,
  title,
  breadcrumbNames,
  lastChangesText,
  heading,
}) => {
  const { i18n, t } = useTranslation();
  const [open, setOpen] = React.useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const isHeading = heading !== "";
  const fullImgUrl = `/images/langPics/${i18n.language}/${imgUrl}`;
  return (
    <BlockLayout
      title={title}
      breadcrumbNames={["Home", ...breadcrumbNames, title]}
    >
      <Box>
        {isHeading && (
          <Typography style={{ marginBottom: 30 }} variant="h4">
            {t(heading)}
          </Typography>
        )}
        <Link style={{ cursor: "pointer" }} onClick={handleClickOpen}>
          <img style={{ width: "100%" }} src={fullImgUrl} alt="Hierarchy" />
        </Link>
        <LastChangesText>
          {fromDigsToFormat(lastChangesText, i18n.language)}
        </LastChangesText>
      </Box>
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          {isHeading ? t(heading) : t(title)}
        </DialogTitle>
        <img style={{ width: "100%" }} src={fullImgUrl} alt="Hierarchy" />
      </Dialog>
    </BlockLayout>
  );
};

export default ImageContainer;
