const useStyle = makeStyles((theme) => ({
  imageContainer: {
    height: 250,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    minWidth: 280,
    justifyContent: "center ",
  },
  imgStyle: {
    maxHeight: 85,
    maxWidth: 280,
    [theme.breakpoints.down("xs")]: {
      maxWidth: 210,
    },
  },
}));

const Partners = () => {
  const [numberOfSlides, setNumberOfSlides] = React.useState(null);
  const { t } = useTranslation();
  const handleWindowChange = () => {
    const windowWidth = window.innerWidth;
    if (windowWidth <= breakpoints.sm) setNumberOfSlides(1);
    else if (windowWidth <= breakpoints.md) setNumberOfSlides(2);
    else if (windowWidth <= breakpoints.lg) setNumberOfSlides(3);
    else if (windowWidth <= breakpoints.xl) setNumberOfSlides(4);
    else setNumberOfSlides(5);
  };

  React.useEffect(() => {
    handleWindowChange();
    window.addEventListener("resize", handleWindowChange);
    return () => window.removeEventListener("resize", handleWindowChange);
  }, []);

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: numberOfSlides,
    slidesToScroll: 1,
    autoplay: true,
    nextArrow: <Arrow direction="right" withoutMargin />,
    prevArrow: <Arrow direction="left" withoutMargin />,
  };

  return (
    <Block withBackground>
      <Title>{t("Our partners")}</Title>
      <Slider {...settings}>
        {items.map((item, i) => (
          <PartnerItem key={i} item={item} />
        ))}
      </Slider>
    </Block>
  );
};

function PartnerItem(props) {
  const classes = useStyle();
  const { t } = useTranslation();
  return (
    <Box className={classes.imageContainer} mr={2}>
      <Link
        component={"a"}
        rel="noopener"
        target={"_blank"}
        href={props.item.link}
      >
        <img
          className={classes.imgStyle}
          alt={props.item.name}
          src={props.item.imageUrl}
        />
      </Link>

      <Typography
        style={{
          margin: "20px",
          fontWeight: "bolder",
          textAlign: "center",
        }}
      >
        <Link
          component={"a"}
          rel="noopener"
          target={"_blank"}
          href={props.item.link}
        >
          {t(props.item.name)}
        </Link>
      </Typography>
    </Box>
  );
}

export default Partners;
