import React from "react";
import {
  Table,
  TableBody,
  TableRow,
  TableCell,
  TableHead,
  Typography,
  TableContainer,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { useTranslation } from "react-i18next";
import clsx from "clsx";

const useStyles = makeStyles({
  headBold: {
    fontWeight: "normal",
    color: "#FFFFFF",
  },
  marginTable: {
    marginTop: 20,
  },
  headColor: {
    backgroundColor: "#03A3DF",
    opacity: "50%",
  },
  greenTitle: {
    color: "#27AE60",
  },
  redTitle: {
    color: "#EB5757",
  },
  container: {
    width: "100%",
    overflowX: "auto",
  },
});

const keysOfTable = {
  uz: {
    numberOfUnits: "number",
    unit: "unit_uz",
    price: "price",
    statusOfUnit: "status",
  },
  ru: {
    numberOfUnits: "number",
    unit: "unit_ru",
    price: "price",
    statusOfUnit: "status",
  },
  en: {
    numberOfUnits: "number",
    unit: "unit_en",
    price: "price",
    statusOfUnit: "status",
  },
};

const TenderTableStart = ({ item }) => {
  const classes = useStyles();
  const { i18n, t } = useTranslation();
  const currentLanguage = i18n.language;

  return (
    <TableContainer className={classes.container}>
      <Table className={classes.marginTable}>
        <TableHead className={classes.headColor}>
          <TableRow>
            {Object.keys(keysOfTable[currentLanguage]).map((key, i) => (
              <TableCell key={i} align="center">
                <Typography className={classes.headBold}>
                  {t(`tenderDetailInfoTable.${key}`)}
                </Typography>
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            {Object.values(keysOfTable[currentLanguage]).map((value, i) => (
              <TableCell
                key={i}
                align="center"
                className={clsx(
                  value === "status" &&
                    (item["status"] === "open"
                      ? classes.greenTitle
                      : classes.redTitle)
                )}
              >
                <Typography>
                  {value !== "status"
                    ? item[value]
                    : item["status"] === "open"
                    ? t("tenderDetailInfoTable.notSold")
                    : t("tenderDetailInfoTable.sold")}
                </Typography>
              </TableCell>
            ))}
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TenderTableStart;
